.column-last {
  margin-top: 2.5vh;
}

.user-address {
  text-decoration: none;
  color: whitesmoke;
}

.user-address:hover {
  text-decoration: none;
  color: whitesmoke;
}

.user-icon {
  margin-right: 0.2rem;
}
.column-last > span {
  font-size: 1rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.landingpage {
  min-height: 100vh;
  min-width: 100vw;
  color: rgb(175, 173, 171);
}

.left-column {
  background-image: linear-gradient(rgb(27, 27, 27), rgb(34, 34, 34));
  height: 100%;
  min-height: 150vh;
}

.middle-column {
  background-image: linear-gradient(rgb(17, 17, 17), rgb(24, 24, 24));
  border-left: rgb(59, 59, 59) solid 1px;
  border-right: rgb(59, 59, 59) solid 1px;
  height: 100%;
  min-height: 150vh;
}

.right-column {
  background-image: linear-gradient(rgb(27, 27, 27), rgb(34, 34, 34));
  height: 100%;
  min-height: 150vh;
}

.head-container {
  /* padding: 3vh 1vw; */
  padding-bottom: 30px;
}
.head-row {
  border-bottom: 1px solid rgb(32, 32, 32);
  padding: 3vh 1vw;
}

.username {
  font-size: 1.4rem;
  font-weight: 700;
}

.bio {
  font-size: 1.3;
  font-weight: 400;
}

.card {
  text-align: left;
  width: 100%;
  margin: 1.5vh 1vw;
  background-color: rgb(39, 38, 38);
}
.link-button {
  width: 100%;
}

.link {
  width: 100%;
}

.become-creator-button {
  float: right;
}

.paper {
  padding: 20px;
}

.left-column-container {
  margin: 0;
  padding: 10vh 3vw 6vh;
  min-height: 100vh;
}

.left-column-button {
  margin: 10px;
  border-radius: 20px;
  padding: 1vh 2vw;
  width: 100%;
}

.input-form {
  max-width: 50vw;
  margin: 20vh auto 0;
  justify-content: left;
  text-align: justify;
}

.input {
  background-color: rgb(29, 28, 28);
  color: white;
  font-size: 20px;
}
.input:focus {
  background-color: rgb(29, 28, 28);
  border: 2px solid whitesmoke;
  color: white;
}

.input-prepend {
  background-color: rgb(29, 28, 28);
  color: aliceblue;
}

.profile-image-input {
  align-self: start;
  font-size: 12px;
  padding: 2px 30vw 2px 0;
}

.profile-image {
  border: none;
}

.blog-upload-form {
  width: 70%;
  margin: 3vw auto;
  text-align: left;
}
.profile-image-input {
  width: 50vw;
}

.submit-button-column .submit-button {
  float: right;
  margin-top: 20px;
}

.dropdown {
  padding: 1vh 1vw 0;
}

.blog-image {
  border: none;
}

.submit-button {
  float: right;
}

.right-column {
  padding-top: 2vh;
}

.creator-button {
  padding: 3px 6vw;
  font-size: 1rem;
}

.mint-button {
  margin: 0 20px;
}

.blog {
  padding: 20px;
}

.blog-body {
  max-height: 20vh;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2; /* number of lines to show */
  -webkit-box-orient: vertical;
  font-family: "Roboto", sans-serif;
}

.blog-title {
  font-weight: 700;
}

.blogs-wrapper {
  margin: 2vh 2vw;
}

.link-to-blog-button .link-to-blog {
  width: 100%;
  min-width: 50vw;
}

.blog-title-detailed-blog {
  padding: 3vh 2vw;
  font-size: 1.5rem;
  font-weight: 700;
}
.blog-body-detailed-blog {
  font-size: 1.1rem;
  font-weight: 400;
  /* overflow: ; */
}

.card-wrapper-detailed-blog {
  margin: 3vh 3vw;
}

.dropdown {
  float: right;
}
.tip-button {
  float: right;
}

